body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-grey-background {
  background-color: #666
}

.App-white-background {
  background-color: #fff;
}

.footer {
  background-color: #4c4c4c;
  height: 100px;
}

.Header {
  background-color: #4c4c4c;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 100px;
}

.header-btn {
  background: transparent; 
  border: none;
  color: #6666ff;
  font-size: 1rem;
  height: 100%;
  vertical-align: middle;
  width: 100px;
}

.header-recent-projects-btn-dropdown-container {
  display: inline-block;
  height: 100%;
  vertical-align: middle;
  width: 100px;
}

.header-recent-projects-btn-dropdown {
  position: relative;
  z-index: 1;
}

.header-recent-projects-btn-dropdown-btn {
  height: 40px;
  width: 150px;
}

.header-home-btn {
  border-right: 1px solid #fff;
}

.header-help-btn {
  border-right: 1px solid #fff;
}

.header-recent-projects-btn {
  border-left: 1px solid #fff;
  border-right: 1px solid #fff;  
}

.header-login-btn {
  border-left: 1px solid #fff;
}

.header-register-btn {
  border-left: 1px solid #fff;
}

.header-logout-btn {
  border-left: 1px solid #fff;
}

.header-title-container {
  text-align: center;
}

.header-main-title {
  color: #fff;
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 15px;
}

.header-sub-title {
  color: #fff;
  margin-top: 5px;
}


.CardPageTemplate,
.BlogPostTemplate,
.BlogPageTemplate {
  height: 100%;
}

.CardPageTemplate * ,
.BlogPostTemplate * ,
.BlogPageTemplate * {
  background-color: #f2f2f2;
  border: 1px solid #000;
  box-sizing: border-box;
}

.pseudo-btn-space {
  border: none;
  height: 100%;
  width: 40px;
}

.fa-plus-square {
  border: none;
  margin-right: 10px;
  padding: 10px;
  position: absolute;
  left: 0;
  top: 0;
}

.template-header-btn {
  background: transparent; 
  box-sizing: border-box;
  border: 1px solid #000;
  color: #6666ff;
  font-size: 1rem;
  margin: 10px 10px 10px 0;
  vertical-align: middle;
  min-width: 80px;
}

.template-header {
  display: -webkit-flex;
  display: flex;
  height: 100px;
  position: relative;
  width: 100%;
}

.template-card-container {
  -webkit-align-content: center;
          align-content: center;
  background-color: #fff;
  border: none;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  height: calc(100% - 200px);
  -webkit-justify-content: center;
          justify-content: center;
}

.template-card {
  height: 200px;
  margin: 30px;
  width: 300px;
}

.template-page-section {
  height: 200px;
  margin: 30px;
  width: calc(90% - 5px);
}

.template-post-main {
  height: 300px;
  margin: 30px;
  width: calc(70% - 5px);
}

.template-post-sub-container {
  height: 200px;
  margin: 30px;
  width: calc(90% - 5px);
}

.template-post-sub {
  height: 70px;
  margin: 20px auto;
  width: calc(90% - 5px);
}
  
.template-footer {
  height: 100px;
  width: 100%;
}
.TemplateContainer {
  background-color: #fff;
  height: 800px;
  margin: 50px auto;
  width: 80%;
}


.ProjectCard {
  margin:  3%;
  padding: 3%;
  width:   30%;
  height:  auto;
  background-color: #BDCCD8;

  display:         -webkit-flex;

  display:         flex;
  -webkit-flex-direction:  column;
          flex-direction:  column;
  -webkit-flex-wrap:       wrap;
          flex-wrap:       wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items:     stretch;
          align-items:     stretch;

  text-align: center;
}

.ProjectTitle {
  font-size:   150%;
  color:       black;
  font-weight: bolder;
  text-align:  center;
}

.RepoName {
  font-size:  85%;
  color:      #454545;
  text-align: center;
  /* font-weight: bolder; */
}

.Updated {
  font-size:   85%;
  color:       #454545;
  font-weight: bolder;
  text-align:  center;
}

.Created {
  font-size:   70%;
  color:       #454545;
  text-align:  center;
  /* font-weight: bolder; */
}



.GithubCard {
  width:   90%;
  margin:  2%;
  padding: 2%;
  width:   30%;
  height:  auto;
  background-color: #BDCCD8;

  display:         -webkit-flex;

  display:         flex;
  -webkit-flex-direction:  column;
          flex-direction:  column;
  -webkit-flex-wrap:       wrap;
          flex-wrap:       wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items:     stretch;
          align-items:     stretch;
  text-align:      center;
}

.GithubCardMinimum {
  display:         -webkit-flex;
  display:         flex;
  -webkit-flex-direction:  row;
          flex-direction:  row;
  -webkit-flex-wrap:       wrap;
          flex-wrap:       wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items:     stretch;
          align-items:     stretch;
  text-align:      center;
}

.GithubCardHeaders {
  width:           60%;
  display:         -webkit-flex;
  display:         flex;
  -webkit-flex-direction:  column;
          flex-direction:  column;
  -webkit-flex-wrap:       nowrap;
          flex-wrap:       nowrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items:     stretch;
          align-items:     stretch;
  text-align:      left;
}

.GithubCardLabels {
  width:           40%;
  display:         -webkit-flex;
  display:         flex;
  -webkit-flex-direction:  column;
          flex-direction:  column;
  -webkit-flex-wrap:       nowrap;
          flex-wrap:       nowrap;
  -webkit-align-items:     stretch;
          align-items:     stretch;
  text-align:      center;
  font-size:     1.2vw;
}

.GithubCardLabels div {
  width:            100%;
  background-color: white;
  padding:          1%;
  margin:           1%;
  text-overflow: ellipsis;
  overflow:      hidden;
  white-space:   nowrap;
}

.CardTitle {
  font-size:   2vw;
  font-weight: bold;
  margin:      1%;
  text-overflow: ellipsis;
  overflow:      hidden;
  white-space:   nowrap;
}

.CardNumber {
  font-size:   1.7vw;
  font-weight: bolder;
}

.CardDescription {
  font-size:        1.7vw;
  margin-top:       3%;
  text-align:       left;
  background-color: lightgrey;
  width:            90%;
  padding:          2%;
  -webkit-align-self:       center;
          align-self:       center;
}

.profile-select-options {
  background-color: #4c4c4c;
  height: 200px;
  margin: 40px auto;
  text-align: center;
  width: 80%;
}

.profile-select-options-title {
  color: #fff;
  padding-top: 60px;
}

.profile-select-options-btn-container {
  margin-top: 50px;
}

.recent-projects-container {
  background-color: #4c4c4c;  
  height: 600px;
  margin: 0 auto 70px;
  text-align: center;  
  width: 80%;
}

.recent-projects-container-title {
  color: #fff;
  padding-top: 20px;
}
.Welcome {
  background-color: #4c4c4c;
  height: 800px;
  margin: 50px auto;
  width: 80%;
}

.welcome-content-description-title {
  color: #fff;
  text-align: center;
  padding-top: 50px;
}

.welcome-content-description {
  color: #fff;
  margin: 30px;
}

.welcome-content-instructions-title {
  color: #fff;
  text-align: center;
  padding-top: 100px;
}

.welcome-content-instructions {
  color: #fff;
  margin: 30px;
}
